@import 'ddvar.less';
* {
  box-sizing: border-box;
  font-family: ;
}

body,
html {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
a{cursor:pointer;text-decoration: none;&:hover{text-decoration: none;}}

.diandiancontainer{padding-top: 50px;padding-bottom: 38px;height: 100%;background-color: @colorB;
  nav{position: absolute;background: linear-gradient(to right,darken(@colorA,10%),lighten(@colorA,10%));    box-shadow: 0 0 11px rgba(0,0,0,0.13);border-color:@colorA;color:@colorC;height: 40px;min-height: 30px;
    .navbar-brand{color:@colorC;height: 100%;padding: 10px 15px;}
    .navbar-right a{color:@colorC;}
    .icon-user-circle{padding: 10px 15px;font-size: 12px;position: relative;
      &::before{font-size: 25px;vertical-align: middle;margin-top: -5px;}
      &::after{content: "\e252";font-family: 'Glyphicons Halflings';margin-left: 5px;vertical-align: middle;}
    }
    .navbar-form{margin: 0px;margin-top: 7px;border:none;
      input{border-radius: 20px;border:none;padding: 5px;height: 25px;font-size: 13px;}}
   }
  .diandiancenter{width: 100%;height: 100%;padding-right: 70px;padding-left: 30%;position: relative;
    .leftsidebar{position: absolute;left:0px;top:0px;height: 100%;padding: 0px;width: 30%;border-right: 1px solid #DDDDDD;background-color: @colorC;
      padding-top: 45px;
    }
    .rightsidebar{position: absolute;right:0px;top:0px;height: 100%;padding: 0px;width:70px;border-left: 1px solid #DDDDDD;background-color: lighten(@colorB,1%);text-align: center;
      a{display: inline-block;width:100%;height: 50px;text-align: center;color:@colorA;
        &::before{font-size: 30px;display: block;text-align: center;margin:10px auto;margin-bottom: 0px;}
        &:hover,&:focus{text-decoration: none;cursor: pointer;}
      }
    }
    .centercontent{height: 100%;padding: 0px;height: 100%;position:relative;padding-top: 45px;padding-left: 10px;padding-right: 10px;padding-bottom: 10px;
      .vuepage{height: 100%;width: 100%;position: relative;overflow: hidden;
      }
      .ptop{padding-top: 58px;
        .topheader{height: 58px;position: absolute;top:0px;left:0px;width:100%;border-bottom: 1px solid #EEEEEE;
          .tright{height: 100%;padding: 15px;display: inline-block;float: right;}
        }
      }
      .vuepagebar{position: absolute;top:0px;left: 0px;width:100%;height: 45px;vertical-align: middle;padding: 0px 10px;font-family: '';
        .bg{border-bottom: 1px solid #EEEEEE;height: 100%;padding: 0px 10px;padding-left: 0px;}
        .bartitle{display: inline-block;float: left;height: 100%;padding: 2px;
          .ddbtn{.glyphicon{margin: 0px;}padding: 0px 10px;}
          .back{color:lighten(@colorA,5%); height: 100%;text-align: center;line-height: 40px;vertical-align:middle;font-size: 13px;}
          .split{background-color: #DDDDDD;margin-top: 0px;height: 22px;}
          .title{padding: 0px 10px;height: 100%;line-height: 40px;vertical-align:middle;}
        }
        .barbutton{display: inline-block;float:right;margin-top: 10px;}
        .title,.smalltitle{display: inline-block;color:@colorA;font-size: 16px;vertical-align: middle;text-decoration: none;padding: 5px 15px;}
        .smalltitle{font-size: 12px;color:darken(@colorA, 23%);
          &::before{content: '>';margin-right: 10px;}
        }
      }
    }
  }
}

.selected {
  .corner-icon {
    border-color: #fe740b #fff #fff #fff;
  }

  .corner-icon span:before {
    font-family: 'Glyphicons Halflings';
    content: "\e013";
    position: relative;
    top: -29px;
    right: 13px;
    font-size: 12px;
    color: white;
  }
}

.transparent {
  opacity: 0.15;
  background-color: @fontcolor;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0 !important;
}

.minicard-container {
  font-size: 0;

  * {
    font-size: initial;
  }
}

.minicard {
  padding: 0 2px 2px 0;

  .minicard-content {
    cursor: pointer;
    width: 100%;
    height: 100%;
    position: relative;
    text-align: center;
    padding: 15px 10px;
    min-height: 30px;
    background-color: white;
    border-right: 1px solid whtie;
    border-bottom: 1px solid whtie;
    border-radius: 2px;
    color: @fontcolor;
    display: inline-block;

    &:focus,
    &:hover {
      background-color: darken(white,1%);
    }
  }
}

.corner-icon {
  width: 0;
  height: 0;
  border-color: darken(#EEEEEE,4.8%) #fff #fff #fff;
  border-style: solid;
  border-width: 25px 0 0 25px;
  position: absolute;
  top: 0;
  right: 0;
  background: none;
}

.group-title {
  padding: 10px;
}

.diandianfooter {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 38px;
  text-align: center;
  color: grey;
  font-size: 12px;
  line-height: 37px;
  vertical-align: middle;
  background: none;
  border-top: 1px solid #DDDDDD;
}

.innerpanel{
  background-color: @colorB;
  text-align: left;
  padding: 5px;border-radius: 3px;
  .innerpanel-title{position: relative;border-bottom: 1px solid #DDDDDD;
    p{display: inline-block;width: 100%;line-height: 30px;margin: 0px;}
    padding-right: 68px;
    .tright{position: absolute;right: 0px;top:0px;height: 100%;}
  }
  .innerpanel-content{padding-left: 28px;font-size: 12px;padding: 5px;position: relative;height: 120px;overflow: hidden;
    p{padding-left: 30px;position: relative;padding-top: 5px;
    &>i{color:darken(#DDDDDD,10%); padding-right:10px;font-size: 18px;position: absolute;top:5px;left: 0px;}}

    .left{width: 100%;padding-right: 155px;}
    .right{height: 100px;width:120px;position: absolute;right: 0px;top:0px;padding-top: 5px;}
  }
}

.attachments{display: block;word-break:break-all;
  a{margin-right: 5px; margin-bottom: 5px;background-color:@colorB;padding: 8px 15px;position: relative;padding-right: 40px;display: inline-block;border-radius: 2px;
    span{margin-left: 5px;display: inline-block;width:35px;height: 36px;text-align: center;position: absolute;right: 0px;top:0px;color:#FF6666;
      line-height: 35px;vertical-align: middle;&:hover{background-color: darken(@colorC,8%);}
    }
  }
  .deleted{text-decoration:line-through;background-color: #FF6666;color: white;
    span{color:#66CC66;
      &:hover{background-color: lighten(#FF6666,5%)}
    }
  }
}

.user{font-size: 0px;padding-left: 0px;margin: 0px;padding: 0px;padding-top: 5px;}

.userphoto{display: inline-block;font-size: 13px;margin-bottom: 5px;margin-right: 5px;
  &::before{content: '';display: inline-block;width: 38px;height: 38px;border-radius: 28px;background-color:darken(#f5f5f9,10%);vertical-align: middle;margin-right: 2px;}
}

.dayinfo {
  padding: 10px;
  .day {
    display: inline-block;
    width: 150px;
    height: 160px;
    text-align: center;
    line-height: 160px;
    vertical-align: middle;
    background-color: white;
    color: @colorA;
    font-family: fantasy;
    position: relative;

    label {
      font-size: 50px;
    }

    .down,
    .up {
      position: absolute;
      left: 0;
      text-align: center;
      width: 100%;
      line-height: normal;
      color: black;
    }

    .up {
      top: 20px;
    }

    .down {
      bottom: 20px;
    }
  }
}

.side_task{
  width:100%;height: 100%;padding:10px;
  h4{color:@titlefontcolor;}
}

.contenttoolbar{
  margin-left: 20px;height: 100%;color:@titlefontcolor;line-height: 50px;vertical-align: middle;
  h4{margin: 0px;display: inline-block;font-family: system-ui;font-size: 18px; letter-spacing: 1px;color:@titlefontcolor;}
}

.innerbar{padding-left:10px; padding-right:10px; padding-top:89px;}

.view{padding:30px 10px;
  .row{margin: 20px auto;padding: 5px;
    .control-label{text-align: right;padding-top: 0px;font-size: 13px;}
    .user{padding-top: 0px;}
  }
}

.infomsg {
    padding: 10px;border-radius: 3px;padding-bottom: 58px;position: relative;
    border-bottom: 1px solid #EEEEEE;
    .infomsg-top {
        padding: 10px;
        color: @colorA;
        i {
            margin-right: 5px;
        }
    }
    .infomsg-bottom{height: 48px;font-size: 0px;border-top: 1px solid @colorB;position: absolute;bottom: 0px;left:0px;width:100%;
      a{display: inline-block;width:50%;height: 100%;font-size: 12px;text-align: center;line-height: 48px;vertical-align: middle;
        &:first-child{border-right: 1px solid @colorB;}
        &:hover{background-color: @colorB;text-decoration: none;}
        &::before{margin-right: 5px;}
      }
    }
}

.msg {
    list-style: none;margin: 0px;padding-left: 0px;
    .msg-item {
        padding-left: 63px;
        position: relative;
        font-size: 12px;
        border-top: 1px dashed #EEEEEE;
        padding-top: 20px;
        padding-bottom: 10px;
        padding-right: 10px;
        &:hover{background-color:@colorB;}
        &:first-child {
            border-top: none;
        }
        .attachment {
            padding: 5px 0;
        }
        .msguser {
            position: absolute;
            top: 20px;
            left: 15px;
        }
        .msg-bottom {
            .fl {
                padding: 5px;
                color: darken(@colorB,15%);
                padding-left: 0;
            }
            a {
                color: darken(@colorB,15%);
                padding: 5px;
                &::before {
                    margin-right: 3px;
                }
            }
        }
    }
}

.pageloding {
  position: absolute;
  top:0px;left:0px;
  display: table;
  width: 100%;
  height: 100%;
  vertical-align: middle;
  z-index: 10000;
  border-radius: 2px;
  text-align: center;
  .pageloding-cell{display: table-cell;width: 100%;height: 100%;text-align: center;vertical-align: middle;}
  .pageloding-cell>div{color:white;
    display:inline-block;padding: 30px 50px;font-size: 12px;border-radius: 20px;position: relative;
    &:before{position: absolute;left:0px;top:0px;background-color: grey;opacity: 0.5;width:100%;height: 100%;content: '';border-radius: 5px;
      border: 1px solid lighten(black,50%);
    }
  }
}

.alertdialog{position: absolute;top: 0px;left:0px;width: 100%;z-index: 100;.alert{border: none;border-radius: 0px;margin-bottom: 0px;border-bottom:1px solid #EEEEEE;}}

.vb > .vb-dragger {
    z-index: 100;
    width: 6px;
    right: 0px;
}

.vb > .vb-dragger > .vb-dragger-styler {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-transform: rotate3d(0,0,0,0);
    transform: rotate3d(0,0,0,0);
    -webkit-transition:
        background-color 100ms ease-out;
        // margin 100ms ease-out,
        // height 100ms ease-out;
    transition:
        background-color 100ms ease-out;
        // margin 100ms ease-out,
        // height 100ms ease-out;
    background-color: lighten(black,88%);
    // margin: 5px 5px 5px 0;
    //border-radius: 20px;
    height: 100%;
    display: block;
}

.vb.vb-scrolling-phantom > .vb-dragger > .vb-dragger-styler {
    background-color: lighten(black,58%);
}

.vb > .vb-dragger:hover > .vb-dragger-styler {
    background-color: lighten(black,58%);
    margin: 0px;
    height: 100%;
}

.vb.vb-dragging > .vb-dragger > .vb-dragger-styler {
    background-color: lighten(black,58%);
    margin: 0px;
    height: 100%;
}

.vb.vb-dragging-phantom > .vb-dragger > .vb-dragger-styler {
    background-color: lighten(black,58%);
}

.has-error{
  .control-label{color:@error !important;}
  .form-control,.list-select{
    border-bottom: 1px solid @error !important;
    box-shadow: none  !important;
  }
}
.help-block{
  margin-top: 5px;
  margin-bottom: 0px;
  font-size: 12px;
}

.tooltip {
  display: block !important;
  z-index: 10000;
  &.info {

    .tooltip-inner {
      background: @colorA;
      color: white;
      padding: 5px;
      border-radius: 5px;
      box-shadow: 0 5px 10px @colorA;
      max-width: 550px;
    }

    .tooltip-arrow {
      border-color: @colorA;
    }
  }

  .tooltip-inner {
    background: black;
    color: white;
    border-radius: 16px;
    padding: 5px 10px 4px;
  }

  .tooltip-arrow {
    width: 0;
    height: 0;
    border-style: solid;
    position: absolute;
    margin: 5px;
    border-color: black;
  }

  &[x-placement^="top"] {
    margin-bottom: 5px;

    .tooltip-arrow {
      border-width: 5px 5px 0 5px;
      border-left-color: transparent !important;
      border-right-color: transparent !important;
      border-bottom-color: transparent !important;
      bottom: -5px;
      left: calc(50% - 5px);
      margin-top: 0;
      margin-bottom: 0;
    }
  }

  &[x-placement^="bottom"] {
    margin-top: 5px;

    .tooltip-arrow {
      border-width: 0 5px 5px 5px;
      border-left-color: transparent !important;
      border-right-color: transparent !important;
      border-top-color: transparent !important;
      top: -5px;
      left: calc(50% - 5px);
      margin-top: 0;
      margin-bottom: 0;
    }
  }

  &[x-placement^="right"] {
    margin-left: 5px;

    .tooltip-arrow {
      border-width: 5px 5px 5px 0;
      border-left-color: transparent !important;
      border-top-color: transparent !important;
      border-bottom-color: transparent !important;
      left: -5px;
      top: calc(50% - 5px);
      margin-left: 0;
      margin-right: 0;
    }
  }

  &[x-placement^="left"] {
    margin-right: 5px;

    .tooltip-arrow {
      border-width: 5px 0 5px 5px;
      border-top-color: transparent !important;
      border-right-color: transparent !important;
      border-bottom-color: transparent !important;
      right: -5px;
      top: calc(50% - 5px);
      margin-left: 0;
      margin-right: 0;
    }
  }

  &[aria-hidden='true'] {
    visibility: hidden;
    opacity: 0;
    transition: opacity .15s, visibility .15s;
  }

  &[aria-hidden='false'] {
    visibility: visible;
    opacity: 1;
    transition: opacity .15s;
  }
}

//
@media (min-width: @screen-md-min) {
  .sidebar {
    display: block !important;
    left: 0 !important;
  }

  .main {
    position: relative;
    display: block !important;
    opacity: 1 !important;
    padding-right: 80px !important;

    .panel-title {
      padding-right: 100px;

      .panel-title-toolbar {
        right: 80px;
      }
    }
  }

  .navbar-header .glyphicon-circle-arrow-left {
    display: none;
  }
}
//
@media (max-width: @screen-sm-max) {
  .mCSB_outside + .mCSB_scrollTools {
    right: -6px !important;
  }

  .main,
  .sidebar {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
  }

  .main {
    padding: 40px 0 50px !important;
  }

  .navbar-header .glyphicon-circle-arrow-left {
    display: block;
  }

  .panel-title-toolbar {
    padding-right: 5px;
  }

  .toolbar {
    top: 1px;
    left: 0;
    width: 100%;
    height: 50px;
    top: initial;
    padding: 0;
    font-size: 0;
    border-top: 1px solid @gray-base;

    .toolbar-item {
      display: inline-block;
      width: 20%;
    }
  }

  .minicard-container {
    text-align: center;

    .group-title {
      text-align: left;
    }
  }
}
