@inputboxbgcolor: white;
@inputpadding: 12px;

.tcenter {
  text-align: center;
}

.tleft {
  text-align: left;
}

.tright {
  text-align: right;
}

.fleft {
  float: left;
}

.fright{
  float:right;
}

.fr {
    float: right;
}
.fl {
    float: left;
}

.vtop{vertical-align: top;}

.w50{width:50%;}

.clearfloat{
  &::after{content: '';clear:both;display: block;}
}

.split{display:inline-block;width:1px;background-color:#EEEEEE;vertical-align: middle;height: 20px;margin-top: 10px;}

.underline {
  text-decoration: underline;
}

.ellipsis{
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
}
.multiellipsis{position: relative; overflow: hidden;
&::after{content: "..."; position: absolute; bottom: 0; right: 2px; padding-left: 30px;text-align: left;letter-spacing: 1px;
background: -webkit-linear-gradient(left, transparent, #fff 55%);
background: -o-linear-gradient(right, transparent, #fff 55%);
background: -moz-linear-gradient(right, transparent, #fff 55%);
background: linear-gradient(to right, transparent, #fff 55%);
}
}

.bg {
  background-color: white;
}

.full {
  width: 100%;
  min-height: 100%;
}

.glass {
  background: hsla(0,0%,100%,.5) border-box;
  overflow: hidden;
  width: 100%;
  height: 100%;
  text-shadow: 0 1px 1px hsla(0,0%,70%,.1);

  &:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    -webkit-filter: blur(20px);
    filter: blur(20px);
  }
}

.inputbox {
  position: relative;
  background-color: white;
  color: @fontcolor;
  margin-bottom: 20px;
  font-size: 12px;
  .glyphicon{position: absolute;left:0px;top:5px;font-size: 28px;color:#DDDDDD;}
  input {
    width: 100%;
    border: none;
    padding:10px;
    font-size: 15px;
    border-bottom: 1px solid #EEEEEE;
    padding-left: 45px;
    outline: none;
  }

  .input-group {
    position: relative;
    height: 100%;
    width: 100%;

    .input-group-addon {
      background: none;
      border: none;
      border: none;
      border-bottom: 1px solid #EEEEEE;
    }
  }
}

.inputinfo {
  color: grey;
  font-size: 12px;
  &::before {
    font-family: 'Glyphicons Halflings';
    content: "\e086";
    display: inline-block;
    vertical-align: middle;
  }
}
.inputelse{
  margin-bottom: 10px;
}

.haserror {
  color: #FF6666 !important;

  &::before {
    content: "\e101";
  }
}

.validatecode {
  input {
    padding-right: 100px;
  }

  .button {
    position: absolute;
    top: 1px;
    right: 2px;
    padding: 6px;
    width: 100px;
    color: white;

  }
  .disabled{
    background-color: #DDDDDD;
    color:@colorA;
    &:hover{background-color: #DDDDDD;color:@colorA;}
  }
}

.button {
  display: inline-block;
  width: 100%;
  padding: 20px;
  text-align: center;
  background-color: @colorA;
  vertical-align: middle;
  color: white;
  cursor: pointer;

  .glyphicon {
    margin-left: 10px;
    top: 2px;
  }

  &:hover {
    background-color: darken(@colorA,10%);
    color: white;
  }
}

.ddbtn {
  position: relative;
  display: inline-block;
  width: auto;
  text-align: center;
  padding:10px 15px;
  vertical-align: middle;
  background-color: @colorA;
  color: white;
  cursor: pointer;
  .glyphicon {
    margin-left: 10px;
    top: 2px;
  }
  &:hover{color:white; background-color:lighten(@colorA,5%);}
  input{
    position: absolute;
    top: 0px;
    right: 0px;
    opacity: 0;
    height: 100%;
    width: 100%;
    cursor: pointer;
  }
}
.active{color:white !important; background-color:darken(@colorA,20%)  !important;}
.ddbtn-min{
  .ddbtn;
  padding: 5px 9px;
  .glyphicon{
    margin-left: 0px;
    margin-right: 5px;
  }
}
.ddbtn-icon{
  .ddbtn-min;
  padding: 5px 10px;
  .glyphicon{
      margin-right: 0px;
    }
  .dot{
    position: absolute;top:-2px;right: -2px;background-color: red;padding: 4px;border-radius: 50%;
  }
}
.btngrey{background-color: @colorB;color:@colorA;}
.del{background-color: red;&:hover{background-color: darken(red,20%);color:white;}}
.dddefault{  background-color: @colorA;  &:hover {
    background-color: darken(@colorA,10%);
    color: white;
  }}
.ddempty {
  background: none;
  color: @colorA;
  font-size: 16px;
  &:hover {
    background-color: #EEEEEE;
    color: @colorA;
  }
}

.ddmodal{position: absolute;bottom: 0px;left: 0px;width:100%;max-height: 100%;z-index:101;padding-top: 30px;border:1px solid darken(#EEEEEE,3%);
  box-shadow: 0px 0px 1px 0px darken(@colorB,5%);border-radius: 2px;overflow:hidden;
  .ddlmodal-overlay{position: absolute;top:0px;left:0px;width:100%;height: 100%;background-color:darken(white,5%);opacity: 0.92;}
  .ddmodal-title{padding-right: 50px;position: relative;z-index: 202;height: 30px;position: absolute;top:0px;left: 0px;width:100%;
    background-color: none;color:@colorA;
    .bartitle{float: left;padding: 5px;}
    .closebtn{position: absolute;right: 0px;top:0px;color:black;display: block;width:38px;height: 38px;text-align: center;line-height: 38px;vertical-align: middle;font-size: 18px;
      font-weight: normal;
      &:hover{text-decoration: none;}
    }
  }
  .ddlmodal-content{width:100%;height: 100%;z-index: 200;position: relative;overflow: hidden;
    textarea{height: 58px !important;border-radius: 3px;}
    .form-group{margin-bottom: 0px;}
    .list-select .glyphicon{color:white;}
  }
}

.group {
  width: 100%;
  padding: 5px 10px;
  border-bottom: 1px solid #f5f5f9;
  height: auto;
  .back{position: absolute;left: 5px;top:5px;}
  .right{position: absolute;right: 5px;top:5px;}
  .input-group{margin-top: 4px;
    .form-control{background-color: #f5f5f9;border:1px solid #f5f5f9;box-shadow: none;}
    button{background-color:#f5f5f9;border-left:1px solid #DDDDDD;}
  }
  .clearfloat>button{margin-top: 3px;}
}

.fixed {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
}

.clause {
  padding: 5px;
  text-align: center;
}

.checkbox {
  display: inline-block;
  width: auto;
  background-color: none;
  color: black;
  cursor: pointer;

  &::before {
    content: '';
    display: inline-block;
    width: 20px;
    height: 20px;
    border-radius: 3px;
    background-color: white;
    border: 1px solid @colorA;
    vertical-align: middle;
    margin-right: 5px;
    line-height: 18px;
    font-weight: normal;
    text-align: center;
  }

  &:hover {
    text-decoration: none;
  }
}

.checked {
  border:none;
  &:before {
    font-family: 'Glyphicons Halflings';
    content: "\e013";
    background-color: @colorA;
    color: white;
  }
}

.list-select {
  border-bottom: 1px solid #DDDDDD;
  min-height: 40px;
  position: relative;

  .glyphicon {
    position: absolute;
    right: 5px;
    bottom: 10px;
    display: inline-block;
    width: 20px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    vertical-align: middle;
    top: inherit;
    color: rgba(60,60,60,.5);

    &:hover {
      text-decoration: none;
    }
  }
}

.calendar {
  width: 100%;
  height: 100%;


      .daycell {
        font-size: 20px;

        &:hover {
          background-color: lighten(@colorA,25%);
          color: @colorA;
        }
      }
}

.taskcontainer {

  .task {
    color: @colorA;

    .taskbg {
      width: 100%;
      height: 100%;
      background-color: white;
      padding: 10px;

      &:hover {
        background-color: @colorB;
      }
    }

    .task_header {
      position: relative;
      height: 42px;
      padding-left: 48px;
      padding-right: 85px;
      line-height: 42px;
      vertical-align: middle;

      i {
        background-color: lighten(@colorA, 10%);
        font-size: 30px;
        border-radius: 30px;
        position: absolute;
        left: 0;
        top: 0;
      }

      span {
        position: absolute;
        right: 0;
        top: 0;
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        width: 80px;
        text-align: right;
      }
    }

    .task_content {
      padding: 20px 0;

      .progress {
        margin-bottom: 0;
      }
    }
  }
}

.corner {
  position: relative;
  width: 30px;
  height: 40px;
  border-color: darken(#EEEEEE,4.8%) #fff #fff #fff;
  border-style: solid;
  border-width: 40px 0 0 50px;
  position: absolute;
  top: 0;
  right: 0;
  box-shadow: 0 0 20px black;
  z-index: 10;

  span {
    position: relative;
    top: -30px;
    right: 36px;
    font-size: 12px;
    color: white;
    width: auto;
    color: red;
    width: 50px;
    display: inline-block;
    transform: rotate(38deg);
  }
}

.iconbox {
    display: inline-block;
    width: 30px;
    height: 30px;
    line-height: 30px;
    vertical-align: middle;
    background-color: @colorA;
    color: white;
    text-align: center;
    text-align: center;

    border-radius: 3px;
    &:hover {
        color: white;
        text-decoration: none;
    }
    i.glyphicon {
        display: inline-block;
        vertical-align: middle;
        margin-top: -4px;
    }
    .text{font-size: 12px;padding-right: 5px;margin-left: 5px;}
}
.iconbox-text{
  .iconbox;
  width:auto;
  padding: 0px 5px;
}

.badge{border-radius: 50%;font-size: 12px;font-weight:normal;background-color: @colorA;color:@colorC;padding:0px;width:18px;height: 18px;text-align: center;line-height: 18px;vertical-align: middle;}

.doublearrow::after {
  content: '\e258\e258';
  font-family: 'Glyphicons Halflings';
  vertical-align: top;
  display: inline-block;
  letter-spacing: -6px;
  margin-left: 10px;
  margin-bottom: -1px;
}

.diandian {
  border-radius: 3px;
  .diandian-bar{border-bottom: 1px solid #EEEEEE;padding: 10px;margin: 10px;}
  textarea {
    background-color: @colorB !important;
    height: 150px !important;
    margin-top: 15px;
  }

  .form-control {
    border: none;
    box-shadow: none;
    border-bottom: 1px solid #DDDDDD;
    font-size: 110%;
    border-radius: 0;
    background: none;
    height: 40px;
    padding-bottom: 0;
  }

  .form-group {
    .btn-group {
      margin-top: 7px;

      .active {
        background-color: @colorA;
        color: @colorC;
        border-color: @colorA
      }
    }
  }

  .control-label {
    padding-right: 5px;
    padding-top: 14px;
    color: lighten(black,50%);
    font-family: sans-serif;
    font-size: 105%;
    font-weight: normal;
  }

  span.input-group-addon {
    background: none !important;
    border-bottom: 1px solid #DDDDDD !important;
  }

  .v-select {
    display: inline-block;
    width: 100%;

    .form-control,
    .form-control:focus {
      border: none !important;
      height: 40px;
      line-height: 40px;
    }

    .dropdown-toggle {
      border-top: none;
      border-left: none;
      border-right: none;
      border-radius: 0;
    }

    .selected-tag {
      margin-top: 5px;
      height: auto;
    }
  }

  .single .selected-tag {
    position: absolute;
    left: 0;
    top: 0;
  }

  .datepicker {
    width: 100%;

    .datepicker-popup {
      right: 0;
    }
  }
}

.subtaskcontainer {
    padding: 10px 0;
    font-size: 0;
    margin-right: -1%;
}
@media (max-width: @screen-sm-max){
  .diandian{
  .view{padding: 5px;
    .row{margin: 20px auto;}
    .view-label{width:100%;text-align: left;}
    .view-content{width:100%;text-align: center;padding: 5px;
      .w50{width:100%;text-align: left;}
    }
  }
  }
}
