

.webhome{
    position:relative;width: 100%;padding-bottom: 50px;
    .carousel{
        margin-bottom:0px;border-bottom:3px solid white;position:relative;
        .logo{position:absolute;left:20px;top:10px;display:block;width:100px; height:150px;color:white;font-weight:bolder;font-size:30px;text-align:center;}
        .login{
            padding:10px 20px;font-family:Arial;
            position:absolute;right:10%;top:68px; width:380px;height:360px;border-radius:5px;box-shadow:0px 0px 2px #EEEEEE;z-index:1000;
            color:black;
            .loginbg{position:absolute;top:0px;left:0px;width:100%;height:100%;background-color:white;opacity:0.7;}
            .nav-tabs{
                font-size:0px;z-index:1000;position:relative;
                a{display:inline-block;width:50%;text-align:center;font-size:initial;color:black;height:50px;line-height:50px;vertical-align:middle;
                  &:hover{text-decoration:none;border-bottom:3px solid #dddddd;}
                }
                a.active{border-bottom:3px solid @colorA}
            }
            .tab-content{margin-top:5px;z-index:1000;position:relative;padding-top:10px;font-size:110%;
                .tab-pane{
                    .box{position:relative;padding-bottom:10px;
                        input{width:100%;background-color:white;border:1px solid #EEEEEE;padding:13px 5px;padding-left:60px;}
                        .checkbox{border:1px solid darken(#EEEEEE,10%);width:18px;height:18px;margin-right:5px;background-color:white;}
                        .findpwd,.newuser{float:right;}
                        .newuser{margin-right:10px;}
                    }
                    .validate{margin-bottom:10px;
                        input{width:150px;padding:10px 5px;}
                        img{height:43px;width:100px;margin-left:10px;margin-right:28px;}
                    }
                    i{position:absolute;left:20px;top:12px;font-size:20px;color:#808080;}
                    .loginbtn{
                        width:100%;
                        a{display:block;width:100%;height:53px;background-color:@gray-darker;color:white;line-height:53px;vertical-align:middle;text-align:center;font-size:18px;
                          &:hover{background-color:darken(@gray-darker,10%);text-decoration:none;}
                        }
                    }
                    .qrcode{width:100%;height:100%;text-align:center;padding-top:10px;
                            img{margin-top:15px;}
                    }
                }
            }
        }
    }
    &>.container-fluid{background-color:#EEEEEE;width:100%;min-height:500px;padding-bottom:50px;padding-top:50px;
                     &>.row{height:100%;
                        &>div{height:100%;}
                     .silder{width:70%;margin:20px auto;background-color:white;min-height:500px;}
                     }
    }
    .webheader {
        font-size: 30px;
        color: @colorA;
        border-bottom: 2px solid @colorA;
        padding: 20px 10px 10px;
        a {
            float: right;
            font-size: 15px;
            padding-top: 20px;
        }
        margin-bottom: 20px;
    }
    .webcontent{width:80%; min-width:400px; margin: auto;padding: 10px 20px;
      .webtitle{
        position: relative;color:#2952a3;
        h4{font-weight: bolder;display: inline-block;}
        a{float:right;display: inline-block;color:#2952a3;vertical-align: bottom;padding: 10px 0px;font-size: 12px;}
      }
      label{color:darken(#DDDDDD,10%);font-weight:normal;}
      .inputinfo{float: right;}
      .ok{width:150px;height: 150px;display: inline-block;border-radius: 50%;line-height: 150px;vertical-align: middle;border-radius: 50%;background-color: #09BB07;color:white;
      font-size: 50px;margin: 20px auto;
      }
    }
    .webfooter{text-align: center;font-size: 12px;margin-top: 20px;}
}
